.button {
  cursor: pointer;
  font-weight: 700;
  color: #ffffff;
  background-color: #2c60e3;
  border: none;
  width: 255px;
  height: 48px;
  box-shadow: 0px 4px 20px 1px rgba(44, 96, 227, 0.25);
  border-radius: 5px;
}

.popupBg {
    position: fixed; 
    top: 0; 
    left: 0; 
    width: 100%; 
    height: 100%; 
    background: rgba(131, 131, 131, 0.1);
    backdrop-filter: blur(10px);
  }
  
  .popup {
    position: absolute; 
    background: #ffffff;
    width: 730px; 
    left: 50%; 
    top: 50%; 
    transform: translate(-50%, -50%); 
    padding: 60px 95px;
    text-align: center;
  }
  
  .popup__title {
    margin-bottom: 10px;
    font-size: 30px;
    font-weight: 700;
    color: #333333;
  }
  
  .popup__text {
    margin-bottom: 30px;
    font-size: 20px;
    color: #333333;
  }
  
  .popup__button {
    width: 350px;
  }

  @media (max-width: 1099px) {

    .popup {
        width: 343px;
        padding: 60px 15px;
    }
  
    .popup__title {
        font-size: 18px;
    }
  
    .popup__text {
        font-size: 16px;
    }
  
    .popup__button {
        width: 313px;
    }
  
    .popup__buttonDesktop {
        display: none;
    }
  }

  @media (min-width: 1100px) {
    .popup__buttonMobile {
        display: none;
    }
  }
  